.App {
  text-align: center;
  z-index: -1;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}


/* Header Styling Starts */
/* Header Styling Starts */

.navBar{
  z-index: 1;
  color: aliceblue;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 2.5vw;
  height: 3vw;
  margin: 2vw;
  padding-left: 2vw;
  padding-right: 2vw;
}

.name{
  padding-right: 2vw;
  font-size:3vw;
  display: flex;
  align-items: center;
}

.name img{
  width: 5vw;
}

.navButtons{
  padding-left: 2vw;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.navButtons span{
  width: max-content;
  font-size:1.8vw;
  margin-right: 2vw;
}
.link{
  text-decoration: none;
  color: rgb(255, 255, 255);
}

/* Header Styling Ends */
/* Header Styling Ends */





/* Footer Styling Starts */
/* Footer Styling Starts */


.footer{
  text-align: center;
  color: azure;   
  padding: .5vw;
  position:-webkit-sticky;
  bottom: 0%;
  right: 40%;
  font-size: 1.2vw;
}

/* Footer Styling Ends */
/* Footer Styling Ends */






/* Scrolbar Styling Starts */
/* Scrolbar Styling Starts */

::-webkit-scrollbar {
  background-color:black;
  width: 20px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
  background: grey; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: white; 
}

/* Scrolbar Styling Ends */
/* Scrolbar Styling Ends */




/* Background Styling Starts */
/* Background Styling Starts */

.circle{
  background-image: url(../src/image/bg.png);
  background-color: black;
  background-size: cover;
  padding: 50vw;
  margin: 0%;
  border-radius: 50%;
  position:relative;
  top:-20vw;
  right:-25vw;
  z-index:-1;
}

/* Background Styling Ends */
/* Background Styling Ends */



/* First div Styling Starts */
/* First div Styling Starts */

.topic button{
  font-size: 1.4vw;
  font-weight: 700;
  padding: 1vw 2vw 1vw 2vw;
  margin: 1vw  1vw 1vw 0;
  border-radius: 40px;
  background-color: black;
  color: white;
}

.main{
  color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  text-align: left;
  margin: 5vw 6vw 6vw 6vw;
  padding-top: 2vw;
}

.topic{
  font-size:1.5vw;
  width: 30vw;
}

.mainimg img{
  padding-right: 3vw;
  width: 25vw;
}

/* First div Styling Ends */
/* First div Styling Ends */

div span{
  font-size: 1vw;
  color: white;
}


/* Tag Styling Starts */
/* Tag Styling Starts */

.popular{
  width: 100%;
  display: flex;
  justify-content: center;
}

.popularPizzas{
  width: fit-content;
  font-size: 3vw;
  color: white;
  background-color: black;
  font-weight: 800;
  margin-top: 5vw;
  margin-bottom:5vw;
  padding: 2vw;
  letter-spacing: 2px;
  border-radius: 80px;
}

/* Tag Styling Ends */
/* Tag Styling Ends */



/* Card Styling Starts */
/* Card Styling Starts */

.cards{
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.card{
  color: rgb(0, 0, 0);
  width:max-content;
  height: max-content;
  padding: 3vw 1vw 3vw 1vw;
  margin: 3vw;
  border: 1px solid rgb(250, 215, 78);
  border-radius: 5vw;
  background-color:burlywood;
  box-shadow: 2px 5px 10px 5px black;
  font-size: 1vw;
}

.card h1{
  margin: 0%;
  padding: 0%;
}

.card label{
  font-size: 1.6vw;
  font-style:normal;
}
.card img{
  width: 15vw;
}
.card select, .card input{
  width: max-content;
  color: brown;
  font-size: 1.2vw;
  padding-bottom: .5vw;
  padding-left: .3vw;
  font-style:oblique;
  background-color: rgba(240, 248, 255, 0);
  border: none;
  border-bottom: 1px solid black;
}

.card input{
  width: 3vw;
  border: 2px solid;
  padding: .2vw;
  margin-top: 1vw;
  border-radius: 15px;
}


.buy{
  font-size: 1.2vw;
  font-weight: 700;
  padding: 1vw;
  margin:1vw;
  border-radius: 40px;
  background-color: rgb(194, 0, 0);
  color: rgb(255, 255, 255);
  box-shadow: 2px 2px 5px 2px black;
  text-decoration: none;
}

.buy:hover{
  background-color: rgb(28, 148, 1);
  box-shadow: inset 2px 2px 10px 1px black;
}

/* Card Styling Ends */
/* Card Styling Ends */




/* Delivery div Styling Starts */
/* Delivery div Styling Starts */

.delivery{
  width: 100%;
  display:flex;
  padding-top: 2vh;
  justify-content: space-around;
  align-items: center;

}

.deliveryimg img{
  width:25vw;
}

.deliverytext{
  text-align: left;
  color: white;
  width: 40%;
  font-size:1.3vw;
  margin: 0%;
}

/* Delivery div Styling Ends */
/* Delivery div Styling Ends */





/* Offer div Styling Starts */
/* Offer div Styling Starts */

.offer{
  margin-top: 10vw;
  background-color: black;
  display: flex;
  padding: 2vw 2vw 4vw 2vw;
}

.offer button{
  color:rgb(174, 4, 4);;
  width: 19vw;
  font-size: 1.5vw;
  letter-spacing: 1px;
  font-weight: 600;
  border-radius: 20px;
  border: 2px solid white;
  padding: 1vw;
  margin: 1vw;
  background-color: rgb(0, 0, 0);
}

.offer button:hover{
  color: white;
  letter-spacing: 3px;
  font-weight: bold;
  background-color: rgb(16, 195, 0);
  box-shadow: 2px 2px 5px rgb(72, 72, 72);
  border-color: rgba(255, 255, 255, 0);
  
}

.offeri1,.offeri2{
  width: 50%;
}

.offeri2{
  display: flex;
  justify-content: center;
}

.offeri2 img{
  padding-left: 10vw;
  width: 25vw;
}

.offerinput input{
  color: white;
  font-size: 1.5vw;
  margin: 1vw;
  align-items: left;
  padding: 1vw;
  background-color:  rgb(174, 4, 4);
  border:none;
  outline-style:none;
  border-radius: 12px;
}

.offerinput input::placeholder{
  color: black;
}

/* Offer div Styling Ends */
/* Offer div Styling Ends */




/* About Page Styling Starts */
/* About Page Styling Starts */

.profile{
  text-align: left;
  display: flex;
  justify-content: space-between;
  padding:4vw;
}

.profile-img{
  border-radius: 200px;
  overflow: hidden;
  width: 15vw;
}

.fs{
  min-height: 70vh;
  margin:3vh;
  padding-top: 4vh;
  font-size: larger;
  color: white;
}

.about{
  background-color: aliceblue;
  color: black;
  margin: 0%;
  padding-bottom: 2vh;
  text-align:right;
  padding-right: 4vw;
  font-size: 1.2vw;

}

.about-flex{
  display: flex;
  justify-content: space-between;
}

.about-img{
  width: 20vw;
  padding-left: 10vw;
}

/* About Page Styling Ends */
/* About Page Styling Ends */




/* Order Page Styling Starts */
/* Order Page Styling Starts */

.order{
  display: flex;
  justify-content: space-between;
  text-align: left;
  align-items: center;
  margin: 3vw 15vw 3vw 15vw;
}

.order div img{
  width: 18vw;
}

.fs.color{
  min-height: 65vh;
  color: rgb(0, 0, 0);
  font-size: 1vw;
  background-color: rgba(255, 255, 255, 0.693);
  background-image: url(../src/image/bg7.png);
  background-size:contain;
  padding-top: .75vh;
  margin: 4vw;
  box-shadow:2px 2px 10px 2px rgb(0, 0, 0);
}

.fs button{
  align-items: center;
  width: max-content;
  border-radius: 12px;
  padding: 1.5vh 2vw 1.5vh 2vw;
  background-color:rgb(45, 174, 45);
  border: 0px solid;
  box-shadow: 3px 2px 10px 1px rgb(29, 29, 29);
  margin-bottom: 3vh;
}

.orderbutton{
  font-size: 1.2vw;
  font-weight: 550;
  text-decoration: none;
  color:black ;
}

.fs button:hover{
  background-color:rgb(0, 255, 72);
  box-shadow:0px 2px 10px 1px rgb(0, 255, 72);
}

/* Order Page Styling Ends */
/* Order Page Styling Ends */





/* Details Page Styling Starts */
/* Details Page Styling Starts */

.details{
  display: flex;
  justify-content: center;
}

.details .child{
  color: rgb(255, 255, 255);
  width: 85vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: left;
  flex-wrap: wrap;
  align-items:baseline;
  padding: 2vw;
  background-color: rgba(5, 34, 34, 0.873);
  background-image: url(../src/image/bg7.png);
  background-position: 96vw;
  background-size: contain;
  font-size: 1.5vw;
  font-weight: 700;
  text-decoration: solid;
}
.child div{
  background-color:rgba(0, 0, 0, 0);
  display: flex;
  justify-items: center;
align-items: center;
}

.child div input, .child div select, .child div textarea{
  width: 30vw;
  font-size: 1.2vw;
  padding: 1vw;
  margin: 1vw;
}
.detailsbutton{
  margin-left: 10vw;
}


/* Details Page Styling Ends */
/* Details Page Styling Ends */